body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.body {
  background: #ff670f;
  background: -webkit-linear-gradient(top, #ff670f 0%,#ff670f 100%);
  background: linear-gradient(to bottom, #ff670f 0%,#ff670f 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff670f', endColorstr='#ff670f',GradientType=0 );
  font-family: 'Raleway', sans-serif;
}


.container {
    display: flex;
    justify-content: center
}

.center {
    display: flex;
    justify-content: center
}

.image {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    padding: 10px;
    margin: 30px;
}

.card {
    border-radius: 5mm;
    background-color: #333; 
    border-color: #333;
    height: 300px;
    padding: 20px;
    margin: 50px;
}

CardTitle {
    z-index: -1;
    
}
.about {
    background-color: darkslategrey;
    padding: 20px;
    margin-top: 50px;
    color: aliceblue;
}

Col{
    border-color: black;
}
